.popup__input {
    border: 0;
    border-bottom: 1px solid rgba(0, 0, 0, .2);
    font-weight: 400;
    font-size: 14px;
    line-height: 1.21;
    color: #000000;
    padding: 0 0 8px;
    box-sizing: border-box;
}

.popup__input:focus {
    outline: none;
}

.popup__input::placeholder {
    color: #C4C4C4;
}